<template>
  <div class="panoramicAnalysis">
    <div class="top">
      <div class="top-title">
        <span class="title-left">坐席群体分析</span>
        <span class="title-right"></span>
      </div>
      <div class="content content-border">
        <div class="content-title">
          <span class="block"></span>
          <span class="text">技能组坐席人数分布</span>
        </div>
        <div class="chart-main">
          <barCharts
            v-if="seatNumebrRes"
            :barChartsData="seatNumebrData"
            :barWidth="40"
            :barType="'bar'"
            @clickBar="getSeatNumebrDetailData"
            :name="'技能组坐席人数分布'"
            @setOption="setOption"
            :maxShowNumber="13"
          ></barCharts>
        </div>
      </div>
    </div>
    <div class="bottom">
      <div class="bottom-title">
        <div class="title-content">
          <span class="title-left">客户群体分析</span>
          <span class="title-right"></span>
        </div>
      </div>
      <div class="content">
        <div class="content-left content-border">
          <div class="content-title">
            <span class="block"></span>
            <span class="text">坐席技能组调用次数分布</span>
            <el-date-picker
              style="width: calc(50% - 100px);margin-left: 100px"
              size="mini"
              v-model="slData"
              type="daterange"
              range-separator="至"
              value-format="yyyy-MM-dd"
              start-placeholder="开始日期"
              @change="handleChange"
              end-placeholder="结束日期"
            ></el-date-picker>
          </div>
          <div class="chart-main content-table">
            <across-bar ref="acrossBar" @sendParams="getParams"></across-bar>
          </div>
        </div>
        <div class="content-right content-border">
          <div class="content-title">
            <span class="block"></span>
            <span class="text">坐席技能组调用次数统计分布</span>
            <el-date-picker
              style="width: 200px;margin:0 20px 0 80px"
              size="mini"
              v-model="tjfbDate"
              type="date"
              value-format="yyyy-MM-dd"
              placeholder="选择日期"
              @change="handleChange2"
            >
            </el-date-picker>
            <el-button
              type="primary"
              icon="el-icon-download"
              size="mini"
              @click="importYxcsExcel"
              >导出excel</el-button
            >
          </div>
          <div class="chart-main content-table">
            <bar-currency ref="barCurrency"></bar-currency>
          </div>
        </div>
      </div>
      <el-dialog
        title="提示"
        :visible.sync="showBarTableDialogVisible"
        width="80%"
      >
        <div class="barTable" v-loading="zxjnTableLoading">
          <el-table
            :data="zxjnTableArray"
            height="250"
            border
            stripe
            :row-class-name="tableRowClassName"
            style="width: 100%"
          >
            <el-table-column
              width="80"
              type="index"
              label="序号"
            ></el-table-column>
            <el-table-column prop="skillGroup" label="技能组"></el-table-column>
            <el-table-column prop="phone" label="来话号码"></el-table-column>
            <el-table-column prop="kfgdh" label="客服工单号"></el-table-column>
            <el-table-column prop="ywlb" label="业务类别"></el-table-column>
            <el-table-column prop="ywzl" label="业务子类"></el-table-column>
            <el-table-column prop="slsj" label="受理时间"></el-table-column>
            <el-table-column prop="zxgh" label="坐席工号"></el-table-column>
            <el-table-column prop="zx" label="坐席"></el-table-column>
            <el-table-column prop="sfhl" label="推荐是否合理"></el-table-column>
          </el-table>
          <div class="block">
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="pages.pageNo"
              :page-sizes="[1, 10, 20, 50]"
              :page-size="pages.pageSize"
              layout="total, sizes, prev, pager, next, jumper"
              :total="pages.total"
            ></el-pagination>
          </div>
        </div>
        <span slot="title">
          <span class="table_title">技能组推荐明细</span>
          <span class="label">技能组：</span>
          <el-input
            size="mini"
            v-model="jnzVal"
            class="input_c"
            placeholder="请输入技能组名称"
          ></el-input>
          <span class="label" style="margin-left: 10px">来话号码：</span>
          <el-input
            size="mini"
            v-model="phoneVal"
            class="input_c"
            placeholder="请输入来电号码"
          ></el-input>
          <span class="label" style="margin-left: 10px">客服工单号：</span>
          <el-input
            size="mini"
            v-model="gdhVal"
            class="input_c"
            placeholder="请输入客户工单号"
          ></el-input>
          <span class="label" style="margin-left: 10px">受理时间：</span>
          <el-date-picker
            class="input_c"
            size="mini"
            v-model="slData"
            type="daterange"
            range-separator="至"
            value-format="yyyy-MM-dd"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
          >
          </el-date-picker>
          <el-button
            type="primary"
            size="mini"
            style="margin: 0px 10px"
            @click="handleSelectDetailed('rs')"
            >查询</el-button
          >
        </span>
        <span slot="footer" class="dialog-footer">
          <el-button @click="showBarTableDialogVisible = false" size="mini"
            >取 消</el-button
          >
          <el-button
            type="primary"
            @click="showBarTableDialogVisible = false"
            size="mini"
            >确 定</el-button
          >
        </span>
      </el-dialog>
      <!--下面占时注释掉 v-if false-->
      <div class="content" v-if="false">
        <div class="content-left content-border">
          <div class="content-title">
            <span class="block"></span>
            <span class="text">坐席匹配坐席主体</span>
          </div>
          <div class="chart-main content-table">
            <themeLabelList
              :labelData="seatLabelData"
              :pageConfig="seatLabelPageConfig"
              @changePage="changePage"
              :showDetailData="seatLabelDetailData"
              @clickDetail="getSeatLabelDetailData"
              @changeDetailPage="changePage"
              @exportExcel="themeLabelExportExcel"
            ></themeLabelList>
          </div>
        </div>
        <div class="content-right content-border">
          <div class="content-title">
            <span class="block"></span>
            <span class="text">坐席匹配号码主体</span>
          </div>
          <div class="chart-main content-table">
            <themeLabelList
              :labelData="userLabelData"
              :pageConfig="userLabelPageConfig"
              @changePage="changePage"
              :showDetailData="userLabelDetailData"
              @clickDetail="getUserLabelDetailData"
              @changeDetailPage="changePage"
              @exportExcel="themeLabelExportExcel"
            ></themeLabelList>
          </div>
        </div>
      </div>
    </div>
    <el-dialog
      v-if="seatNumebrDetailvisible"
      class="dialog"
      title="提示"
      :visible.sync="seatNumebrDetailvisible"
      width="70%"
      :before-close="handleClose"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
    >
      <span slot="title" class="dialog-header">
        <span
          class="header-button"
          :class="detailActive === '1' ? 'active' : ''"
          @click="changeTable('1')"
          >{{ "坐席明细列表" }}</span
        >
        <!--        <span-->
        <!--          class="header-button"-->
        <!--          :class="detailActive==='2'?'active':''"-->
        <!--          @click="changeTable('2')"-->
        <!--        >{{ '技能列表' }}</span>-->
      </span>
      <bdaTable
        v-if="detailActive === '1'"
        class="dialog-table"
        :tableData="seatNumebrDetailData"
        :headerColor="'#1899fa'"
        :isPage="true"
        :pageConfig="seatNumebrDetailPageConfig"
        @changePage="changePage"
      ></bdaTable>
      <bdaTable
        v-if="detailActive === '2'"
        class="dialog-table"
        :tableData="skillNumebrDetailData"
        :headerColor="'#1899fa'"
        :isPage="true"
        :pageConfig="skillNumebrDetailPageConfig"
        @changePage="changePage"
      ></bdaTable>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="detailListExportExcel"
          >导出Excel</el-button
        >
        <el-button @click="handleClose">关闭</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import Interface from "@/base/mixin/seat-skills";
import bdaTable from "@/components/bda-table/bda-table";
import barCharts from "@/components/bar-charts/bar-charts";
import themeLabelList from "@/components/theme-label-list/theme-label-list";
import acrossBar from "./components/acrossBar";
import barCurrency from "./components/barCurrency";
import { exportExcelGet } from "@/utils/export";
import { CP_PATH } from "@/config";
import date from "../../utils/date";

export default {
  components: {
    bdaTable,
    barCharts,
    themeLabelList,
    acrossBar,
    barCurrency,
  },
  name: "panoramicAnalysis",
  mixins: [Interface],
  data() {
    return {
      // 技能组坐席人数分布
      seatNumebrData: {},
      seatNumebrRes: false,
      //坐席关注标签
      seatLabelData: [],
      seatLabelQueryForm: {
        menuType: "0",
        current: 1,
        size: 10,
      },
      //分页设置
      seatLabelPageConfig: {
        name: "seatLabel",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40, 50],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0,
      },
      //用户关注标签
      userLabelData: [],
      userLabelQueryForm: {
        menuType: "0",
        current: 1,
        size: 10,
      },
      //分页设置
      userLabelPageConfig: {
        name: "userLabel",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40, 50],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0,
      },
      //标签详情
      seatLabelDetailData: {},
      seatLabelDetailQueryForm: {
        dbcolumnAS: "",
        menuType: "0",
        tagName: "",
        current: 1,
        size: 10,
      },
      //分页设置
      seatLabelDetailPageConfig: {
        name: "seatLabelDetail",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40, 50],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0,
      },
      //标签详情
      userLabelDetailData: {},
      userLabelDetailQueryForm: {
        dbcolumnAS: "",
        menuType: "0",
        tagName: "",
        current: 1,
        size: 10,
      },
      //分页设置
      userLabelDetailPageConfig: {
        name: "userLabelDetail",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40, 50],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0,
      },
      //表头颜色
      headerColor: "",
      //弹出框
      detailActive: "1",
      seatNumebrDetailvisible: false,
      seatNumebrDetailData: {},
      seatNumebrDetailForm: {
        current: 1,
        size: 10,
      },
      //分页设置
      seatNumebrDetailPageConfig: {
        name: "seatNumebrDetail",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0,
      },
      //技能明细
      skillNumebrDetailData: {},
      skillNumebrDetailForm: {
        current: 1,
        size: 10,
      },
      //分页设置
      skillNumebrDetailPageConfig: {
        name: "skillNumebrDetail",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0,
      },

      //图表组件数据
      setOptionData: {},

      //坐席技能组调用次数分布
      pages: {
        pageNo: 1,
        pageSize: 10,
        total: 0,
      },
      showBarTableDialogVisible: false, //弹窗
      zxjnTableLoading: false, //加载
      zxjnTableArray: [],
      slData: [],
      jnzVal: "",
      phoneVal: "",
      gdhVal: "",
      tjfbDate: "",
    };
  },
  mounted() {
    this.tjfbDate = date(new date(), "yyyy-MM-dd");
    this.slData = ["2021-04-01", date(new date(), "yyyy-MM-dd")];
    this.getSeatNumebrData();
    this.getSeatLabelData();
    this.getUserLabelData();
    window.addEventListener("resize", () => {
      if (this.setOptionData["技能组坐席人数分布"])
        this.setOptionData["技能组坐席人数分布"].resize();
    });
    this.GetSkillGruopInvokeStatistics(); //进入获取技能组推荐调用统计分布接口
    this.GetSkillGruopInvokeCount(); //获取技能组推荐调用次数统计分布接口
  },
  methods: {
    //导出有效次数
    importYxcsExcel() {
      window.location.href =
        CP_PATH +
        "/skillGruopInvokeCount/downSkillGruopInvokeDetails?date=" +
        this.tjfbDate;
    },
    /**
     * 坐席技能组调用次数分布
     * */
    handleSizeChange(val) {
      this.pages.pageSize = val;
      this.handleSelectDetailed();
      console.log(`每页 ${val} 条`);
    },
    handleCurrentChange(val) {
      this.pages.pageNo = val;
      this.handleSelectDetailed();
      console.log(`当前页: ${val}`);
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 === 0) {
        return "success-row";
      }
      return "";
    },
    handleSelectDetailed(type = "") {
      if (type === "rs") {
        this.pages.pageSize = 10;
        this.pages.pageNo = 1;
      }
      this.zxjnTableLoading = true;
      this.getIntentionInvokeDetails({
        skillGroup: this.jnzVal,
        phone: this.phoneVal,
        kfgdh: this.gdhVal,
        startDate: this.slData[0],
        endDate: this.slData[1],
        sfhl: "",
        size: this.pages.pageSize,
        current: this.pages.pageNo,
      }).then((res) => {
        this.zxjnTableLoading = false;
        this.zxjnTableArray = res.records;
        this.pages.total = res.total;
        window.console.log(res, "kkk");
      });
    },
    GetSkillGruopInvokeStatistics() {
      this.getSkillGroupInvokeStatistics({
        startDate: this.slData[0],
        endDate: this.slData[1],
      }).then((res) => {
        let data = [["score", "product"]]; //--首项保持与组件一致
        for (let i = 0; i < res.length; i++) {
          data.push([res[i].total, res[i].skillGroup]);
        }
        this.$refs.acrossBar.init(data);
      });
    },
    getParams(v) {
      this.jnzVal = v.name;
      this.showBarTableDialogVisible = true;
      this.pages.pageSize = 10;
      this.pages.pageNo = 1;
      this.handleSelectDetailed();
    },
    handleChange() {
      this.GetSkillGruopInvokeStatistics();
    },
    /**
     * 坐席技能组调用次数统计分布
     * */
    handleChange2() {
      this.GetSkillGruopInvokeCount();
    },
    GetSkillGruopInvokeCount() {
      this.getSkillGroupInvokeCount({
        date: this.tjfbDate,
      }).then((res) => {
        let seriesBar0 = [];
        let seriesBar1 = [];
        let datax = [];
        res.forEach((item) => {
          seriesBar0.push(item.total);
          seriesBar1.push(item.invokeCount);
          datax.push(item.dysj);
        });
        this.$refs.barCurrency.init(seriesBar0, seriesBar1, datax);
        window.console.log(res);
      });
    },
    //图表组件
    setOption(name, val) {
      console.log(name, val);
      this.setOptionData[name] = val;
      console.log(this.setOptionData, " this.setOptionData");
    },
    //主题标签导出
    themeLabelExportExcel(data) {
      console.log(data, "themeLabelExportExcel");
      exportExcelGet(
        "/leanSeatingSkillController/exportLabelUserDisplay" +
          "?tagId=" +
          data.tagId
      );
    },
    //客户明细列表导出
    detailListExportExcel() {
      if (this.detailActive === "1")
        exportExcelGet(
          "/leanSeatingSkillController/exportExcelSeatDetails" +
            "?jn=" +
            this.seatNumebrDetailForm.name
        );
      if (this.detailActive === "2")
        exportExcelGet(
          "/leanSeatingSkillController/exportExcelSkillQueue" +
            "?jn=" +
            this.seatNumebrDetailForm.name
        );
    },
    //改变分页
    changePage(data) {
      console.log(data, "data");
      if (data.name === "seatLabel") {
        this.seatLabelQueryForm.size = data.size;
        this.seatLabelQueryForm.current = data.current;
        this.getSeatLabelData();
      }
      if (data.name === "userLabel") {
        this.userLabelQueryForm.size = data.size;
        this.userLabelQueryForm.current = data.current;
        this.getUserLabelData();
      }
      if (data.name === "seatLabelDetail") {
        this.seatLabelDetailQueryForm.dbcolumnAS = data.keyValue || "";
        this.seatLabelDetailQueryForm.size = data.size;
        this.seatLabelDetailQueryForm.current = data.current;
        this.getSeatLabelDetailData();
      }
      if (data.name === "userLabelDetail") {
        this.userLabelDetailQueryForm.dbcolumnAS = data.keyValue || "";
        this.userLabelDetailQueryForm.size = data.size;
        this.userLabelDetailQueryForm.current = data.current;
        this.getUserLabelDetailData();
      }
      if (data.name === "seatNumebrDetail") {
        this.seatNumebrDetailForm.size = data.size;
        this.seatNumebrDetailForm.current = data.current;
        this.getSeatNumebrDetailData();
      }
      if (data.name === "skillNumebrDetail") {
        this.skillNumebrDetailForm.size = data.size;
        this.skillNumebrDetailForm.current = data.current;
        this.getSkillNumebrDetailData();
      }
    },
    //切换技能组坐席人数详情
    changeTable(val) {
      this.detailActive = val;
      if (val === "1") {
        this.seatNumebrDetailForm.current = 1;
        this.seatNumebrDetailForm.size = 10;
        this.getSeatNumebrDetailData();
      }
      if (val === "2") {
        this.skillNumebrDetailForm.current = 1;
        this.skillNumebrDetailForm.size = 10;
        this.getSkillNumebrDetailData();
      }
    },
    //关闭坐席详情
    handleClose() {
      this.detailActive = "1";
      this.seatNumebrDetailForm.current = 1;
      this.seatNumebrDetailForm.size = 10;
      this.skillNumebrDetailForm.current = 1;
      this.skillNumebrDetailForm.size = 10;
      this.seatNumebrDetailvisible = false;
    },
    // 获取地区局
    getRegionList() {},
    // 获取坐席标签分布
    getSeatNumebrData() {
      this.seatNumebrRes = false;
      this.skillSelectSkillGroupSeatDistribution({}).then((res) => {
        let dataMap = [];
        let xNameData = [];
        res.forEach((e) => {
          dataMap.push(e.count);
          xNameData.push(e.name);
        });
        this.seatNumebrData = {
          dataMap: {
            坐席标签分布: dataMap,
          },
          xNameData: xNameData,
          colorMap: {
            坐席标签分布: [
              // '#55BFFF', '#2790FF'
              ["#2790FF", "#55BFFF"],
              ["#19A7CD", "#66D6FF"],
              ["#19CDAE", "#77E7BE"],
              ["#29C644", "#8AE671"],
            ],
          },
          xyName: {
            x轴: { name: "技能", color: "#1A84F4", diffcolor: true },
            y轴: [{ name: "坐席数", color: "#808080" }],
          },
        };
        this.seatNumebrRes = true;
      });
    },
    //获取坐席标签详情
    getSeatNumebrDetailData(data) {
      this.seatNumebrDetailData = {};
      if (data) this.seatNumebrDetailForm.name = data.name;
      this.selectSeatDetails({
        jn: this.seatNumebrDetailForm.name,
        size: this.seatNumebrDetailForm.size,
        current: this.seatNumebrDetailForm.current,
      }).then((res) => {
        this.seatNumebrDetailPageConfig.total = res.total;
        this.seatNumebrDetailPageConfig.size = this.seatNumebrDetailForm.size;
        this.seatNumebrDetailPageConfig.current = this.seatNumebrDetailForm.current;
        this.seatNumebrDetailData = {
          tableData: [...res.records],
          columnNameData: [
            { prop: "zxgh", label: "坐席工号" },
            { prop: "zxmc", label: "坐席名称" },
            { prop: "jn", label: "坐席技能" },
          ],
        };
        this.seatNumebrDetailvisible = true;
      });
    },
    //获取坐席技能详情
    getSkillNumebrDetailData(data) {
      this.skillNumebrDetailData = {};
      this.selectSkillQueue({
        jn: this.seatNumebrDetailForm.name,
        size: this.skillNumebrDetailForm.size,
        current: this.skillNumebrDetailForm.current,
      }).then((res) => {
        this.skillNumebrDetailPageConfig.total = res.total;
        this.skillNumebrDetailPageConfig.size = this.skillNumebrDetailForm.size;
        this.skillNumebrDetailPageConfig.current = this.skillNumebrDetailForm.current;
        this.skillNumebrDetailData = {
          tableData: [...res.records],
          columnNameData: [
            { prop: "jn", label: "坐席技能" },
            { prop: "jnzmc", label: "技能队列" },
          ],
        };
      });
    },

    // 获取坐席标签列表
    getSeatLabelData() {
      this.seatSubject(this.seatLabelQueryForm).then((res) => {
        this.seatLabelPageConfig.total = res.total;
        this.seatLabelPageConfig.size = this.seatLabelQueryForm.size;
        this.seatLabelPageConfig.current = this.seatLabelQueryForm.current;
        this.seatLabelData = res.list.map((e) => {
          return {
            ...e,
            name: e.tagName,
            text: e.tagDescription,
            value: e.account || 0,
          };
        });

        console.log(this.seatLabelData, "seatLabelData");
      });
    },
    // 获取用户标签列表
    getUserLabelData() {
      this.userPrincipalLabel(this.userLabelQueryForm).then((res) => {
        this.userLabelPageConfig.total = res.total;
        this.userLabelPageConfig.size = this.userLabelQueryForm.size;
        this.userLabelPageConfig.current = this.userLabelQueryForm.current;
        this.userLabelData = res.list.map((e) => {
          return {
            ...e,
            name: e.tagName,
            text: e.tagDescription,
            value: e.account || 0,
          };
        });
      });
    },
    //获取坐席标签详情信息
    getSeatLabelDetailData(data) {
      console.log(data, "datadatadata");
      if (data) {
